import { useAuth0 } from "@auth0/auth0-react";
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import target from "../../images/goal.jpg";
import ladyPutMoneyInPig from "../../images/savings-concept.jpg";
import { SetGoals } from "../../First-login/WelcomeToMonez";
import SavingPig from "../../assets/SavingPig";
import { Card, Avatar, Progress } from "antd";
import {
  EditOutlined,
  EllipsisOutlined,
  SettingOutlined,
  DeleteOutlined,
} from "@ant-design/icons";

const { Meta } = Card;

const SavingGoals = () => {
  const { user } = useAuth0();
  const [savedGoals, setSavedGoals] = useState([]);

  useEffect(() => {
    const fetchGoals = async () => {
      try {
        const res = await fetch(`http://localhost:5000/api/goals/${user.sub}`);
        const json = await res.json();
        console.log(json);
        setSavedGoals(json);
      } catch (error) {
        console.log(error);
      }
    };
    fetchGoals();
  }, []);

  const handleDeleteGoal = async (id) => {
    try {
      await fetch(`http://localhost:5000/api/goals/delete/${id}`, {
        method: "DELETE",
      });
      console.log(`the goal with id of: ${id}, was deleted!`);
    } catch (error) {
      console.log("error:", error);
    }
  };

  return (
    <React.Fragment>
      <Container>
        <Header>
          <div style={{ display: "inline-block" }}>
            <Title>
              Saving Goals
              <br />
            </Title>
            <SubTitle>Add new goals, check out your progress.</SubTitle>
          </div>
          <SavingPig />
          {/* <TitleLogo src={ladyPutMoneyInPig} alt="lady-pig"></TitleLogo> */}
        </Header>
        <SetGoals showGoalNow={true} />
        <Goals>
          {savedGoals &&
            savedGoals.map((goal) => (
              <Card
                key={goal._id}
                type={"inner"}
                style={{
                  width: "300px",
                  cursor: "default",
                  margin: "1rem",
                }}
                cover={
                  <img alt="goal-card" src={require("../../images/goal.jpg")} />
                }
                hoverable={true}
                actions={[
                  <EditOutlined key="edit" />,
                  <EllipsisOutlined key="ellipsis" />,
                  <button
                    key={goal._id}
                    onClick={() => handleDeleteGoal(goal._id)}
                  >
                    <DeleteOutlined />
                  </button>,
                ]}
              >
                <Meta
                  avatar={<Avatar src={user.picture} />}
                  title={goal.goalName}
                  description={goal.goalAmount}
                  style={{
                    marginBottom: "1rem",
                    width: "100%",
                  }}
                />
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    margin: "1rem",
                  }}
                >
                  <Progress type="circle" percent={75} />
                </div>
                <h1 style={{ fontSize: "1.1rem" }}>Time to achieve: </h1>
                <Span>{goal.achieveDate}</Span>
              </Card>
            ))}
          {/* <Goal>
            <h1>Goal name</h1>
            <h3>Time to achieve: </h3>
            <h3>Achieve in precenteges</h3>
            <h3>Achieve in money</h3>
          </Goal>
          <Goal>
            <h1>Goal name</h1>
            <h3>Time to achieve: </h3>
            <h3>Achieve in precenteges</h3>
            <h3>Achieve in money</h3>
          </Goal>
          <Goal>
            <h1>Goal name</h1>
            <h3>Time to achieve: </h3>
            <h3>Achieve in precenteges</h3>
            <h3>Achieve in money</h3>
          </Goal>
          <Goal>
            <h1>Goal name</h1>
            <h3>Time to achieve: </h3>
            <h3>Achieve in precenteges</h3>
            <h3>Achieve in money</h3>
          </Goal>
          <Goal>
            <h1>Goal name</h1>
            <h3>Time to achieve: </h3>
            <h3>Achieve in precenteges</h3>
            <h3>Achieve in money</h3>
          </Goal>
          <Goal>
            <h1>Goal name</h1>
            <h3>Time to achieve: </h3>
            <h3>Achieve in precenteges</h3>
            <h3>Achieve in money</h3>
          </Goal> */}
        </Goals>{" "}
        <GoalsLogo src={target} alt="target"></GoalsLogo>
      </Container>
    </React.Fragment>
  );
};

export default SavingGoals;

export const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

export const Header = styled.header`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.5rem;
  width: 100vw;
  margin-top: 1rem;

  @media (max-width: 700px) {
    width: 99vw;
    flex-direction: column;
  }
`;
const Goals = styled.ul`
  color: #000;
  font-size: 22px;
  display: grid;
  grid-template-columns: 1fr;
  width: 70%;
  gap: 0;

  @media (min-width: 800px) {
    grid-template-columns: 1fr 1fr 1fr;
    margin: 0;
  }
`;
