const TitleLogo = styled.img`
  width: 176px;
  height: 172px;
  border-radius: 50%;
  margin-left: 1rem;
`;

const GoalsLogo = styled.img`
  width: 317px;
  height: 301px;
  border-radius: 50%;
  margin-top: 3rem;
`;

export const Title = styled.div`
  font-weight: 900;
  font-size: 1.8rem;
`;

export const SubTitle = styled.div`
  color: #a443f0;
  font-size: 1.8rem;
`;

export const Span = styled.span`
  color: #8000ff;
  letter-spacing: 0;
  margin-bottom: 2rem;
`;
